@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
:root{
  --red: #ff0905;
  --blue: #0434ff;
  --lightblue: #10ffff;
  --lavender: #FFaFFF;
  --lavender2: lavender;
  --amethyst: #ff76ff;
  --purple: #ff05ff ;
  --violet: #c615ff;
}

canvas {

  z-index: -1 !important;
} 

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  z-index: 1;
  background: linear-gradient(120deg, #c615ff 30%, #0434ff);
  background: linear-gradient(120deg, var(--violet) 30%, var(--blue));
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}
.portcon {
    justify-content: center;
    margin: 0px 0 0 0;
    height: auto;
}

.portcon a {
    text-decoration: none;
}

.albcon {
    width: 350px;
    height: 350px;
    margin: 20px auto;
    padding: 0;
    overflow-y: hidden;
    justify-content: center;
    transition: 300ms;
}

.album {
    position: relative;
    z-index: 1;
    width: 100%;
    transition: 300ms;
}

.albumInfo {
    position: relative;
    pointer-events: none;
    z-index: 999;
    bottom: 350px;
    width: 100%;
    height: 100%;
    padding: 40% 15px;
    color: white;
    background: radial-gradient(circle, rgba(16, 255, 255, 0.71) 0%, rgba(16, 255, 255, 0.2) 100%);
    transition: 300ms;
    opacity: 0;
}

.albumTitle {
    text-shadow: 2px 2px 2px var(--lightblue);
}

.releaseDate {
    text-shadow: -2px -2px 2px var(--lightblue);
}

.alsh {
    content: '';
    z-index: 998;
    position: relative;
    top: 0;
    left: 0;
    opacity: 0.9;
}

.albcon:hover .albumInfo {
    opacity: 1;

}

.albcon:hover .album {
    width: 100%;
}

.albcon:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    box-shadow: 0px 0px 15px 5px var(--lightblue);
    opacity: 1;
}

.portfolio-title {
    color: white;
    text-shadow: 0px 0px 20px var(--lightblue);
    margin-bottom: 25px;
}
.navCon {
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 100px -10px ;
    margin-bottom: 25px;
}

.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.logo {
    color: var(--lightblue);
    font-family: 'Bebas Neue', cursive;
    pointer-events: none;
    border: currentColor 4px solid;
    padding: 0em 0.5em;
    border-radius: 0.25em;
    text-shadow:
        0 0 1em rgba(255, 255, 255, 0.5),
        0 0 1em currentColor;
    box-shadow:
        inset 0 0 0.5em 0 var(--lightblue),
        0 0 0.5em 0 var(--lightblue);
    position: relative;
}

.list a {
    content: '';
    width: 50px;
    height: 50px;
}

.spotify {
    width: 25px;
    height: 25px;
    transition: 300ms;
}

.spotify:hover {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
}

.logo::before {
    content: "";
    position: absolute;
    background: var(--lightblue);
    top: 85%;
    left: 0;
    width: 100%;
    height: 100%;

    -webkit-transform: perspective(5em) rotateX(85deg);

            transform: perspective(5em) rotateX(85deg);
    -webkit-filter: blur(1em);
            filter: blur(1em);

    opacity: 0.7;
}
.outline {
    margin: 50px 50px 0px 50px;
    border: 3px solid var(--lightblue);
    color: white;
    padding: 10%;
    position: relative;
    box-shadow:
        inset 0 0 40px 1px var(--lightblue),
        0 0 25px var(--lightblue)
    ;
}

.outline::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0%;
    left: 0px;
    background: linear-gradient(120deg, var(--blue) 10%, var(--violet) 50%) ;
    opacity: 0.95;
    z-index: -1;
}

.bio {
    padding-right: 0%;
    text-align: left;
    line-height: 24px;
    line-break: strict;
    text-shadow: 0px 0px 5px var(--lightblue);
}
.res-con {
    margin: 10px 50px 50px 50px;
    align-items: center;
}

/* .view {
    text-shadow: 0 0 5px var(--lightblue);
} */

button {
    border: 3px solid var(--lightblue);
    color: white;
    background: rgba(16, 255, 255, 0.35);
    padding: 1%;
    font-size: 36px;
    text-shadow: 0 0 5px var(--lightblue);
    box-shadow:
        inset 0 0 40px 1px var(--lightblue),
        0 0 25px var(--lightblue)
    ;
}

.download {
    width: 16%;
}

.res-con a {
    text-decoration: none;
    color: white;
}

.or {
    text-shadow: 0 0 5px var(--lightblue);
}
